  );
}

async function goMainPage() {
  if(viewModel.studio.openItems && viewModel.studio.openItems.some(item => item.checkedOut && item.modified?.model)) {
    const result = await notification.showConfirm({
                type: "warning",
                title: `You are leaving the page.`,
                text: "Changes you made may not be saved.",
                showCloseIcon: true,
                approveText: "Confirm",
                cancelText: "Cancel",
            });
            if (result.result == "cancel") {
                return;
            }
  }
  router.push("/main/applicationList");
}

function checkDeployConditions() {
  return !viewModel.apps.find((app) => app.ID === viewModel.studio.appId)?.deployParameters?.gitProductRepoId;
}

function logout() {
  executor.execute(
    async () => {
      await executor.executeUseCase<ILogout>(ILogout);
      router.push("/login");
    },
    { loading: true }
  );
}

function showDeployTip() {
  if (checkDeployConditions()) {
    deployTip.value = localization.get("deployTip");
    return deployTip.value;
  } else {
    deployTip.value = "";
    return deployTip.value;
  }
}

